.vieclamdangtuyen-1 {
	@include pad(2rem null);
	background: #f2f2f2;
	@include media-breakpoint-up(lg) {
		padding: r(95px) 0; }
	.wrapper {
		margin-top: 2rem;
		@include pad(1.5rem);
		background: #e6e6e6;
		.search-main {
			.select, input, button, select {
				width: 100%;
				height: 40px; }
			.select {
				margin-bottom: 10px; }
			input {
				@include pad(.5rem 1rem);
				border: 0;
				color: #808080;
				@include fz(r(15px));
				&:focus {
					outline: none; }
				&::placeholder {
					color: #808080; } }
			button {
				margin-top: 10px;
				background: #006ab6;
				border: 0;
				display: flex;
				justify-content: center;
				color: #fff;
				align-items: center;
				@include pad(.3rem 1rem);
				i {
					margin-right: .3rem;
					@include fz(r(20px)); } } }
		.table-wrapper {
			@include fz(r(15px));
			margin-top: r(20px);
			table {
				display: block;
				overflow-x: auto;
				tr {
					th, td {
						@include pad(.5rem);
						text-align: center;
						&:first-child {
							padding-left: 1.5rem;
							text-align: left; }
						@include fz(r(15px));
						a {
							text-decoration: none;
							color: #333; }
						.new {
							display: none; } } }
				thead {
					tr {
						background: #006ab6;
						th {
							color: #fff;
							@include fw(600);
							white-space: nowrap; } } } } } }
	@include media-breakpoint-up(lg) {
		.wrapper {
			@include pad(r(30px));
			.search-main {
				display: flex;
				align-items: center;
				.select, button {
					margin: 0; }
				.select {
					+ .select {
						margin-left: 10px; }
					&:first-child {
						max-width: 125px; }
					&:nth-child(2) {
						max-width: 160px; } }
				input {
					max-width: 370px;
					margin-left: 10px; }
				button {
					max-width: 150px;
					margin-left: 10px; } }
			.table-wrapper {
				table {
					display: table;
					width: 100%;
					tr {
						td, th {
							@include pad(.6rem null);
							&:first-child {
								padding-left: 2rem;
								white-space: nowrap; } } }
					tbody {
						tr {
							cursor: pointer;
							transition: .4s all ease-in-out;
							td {
								&:first-child {
									display: flex;
									justify-content: space-between;
									align-items: center; }
								position: relative;
								transition: .4s font-weight ease-in-out;

								.new {
									@include pad(null .75rem);
									display: inline-flex;
									height: 20px;
									align-items: center;
									@include bor-rad(100px);
									background: #006ab6;
									color: #fff;
									span {
										@include fz(r(13px)); } } }
							&:nth-child(odd) {
								background: #fff; }
							&:hover {
								background: #b8cddc;
								td {
									@include fw(700); } } } } } } } } }
