main {
	@include media-breakpoint-down(md) {
		padding-top: r(47px); } }
.phanam-header-1 {
	position: fixed;
	// position: sticky
	top: 0;
	width: 100%;
	background: white;
	// border-top: 2px solid color-get($mau,dark)
	box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.1);
	z-index: 999;
	@include media-breakpoint-down(md) {
		overflow: hidden;
		// position: fixed
		width: 100%; }
	.menu-bar {
		// margin-top: -2px
		display: flex;
		.logo {
			@include pad(.5rem null);
			@include flex-center();
			position: absolute;
			background: color-get($mau, trang);
			top: r(10px);
			@include media-breakpoint-down(lg) {
				top: r(40px); }
			@include media-breakpoint-down(md) {
				top: 0; }
			&:after {
				right: 0;

				bottom: -1rem;
				z-index: -1;
				content: "";
				background: color-get($mau, trang);
				display: block;
				position: absolute;
				width: 300%;
				height: 100%;
				box-shadow: -6px 5px 5px 0px rgba(0, 0, 0, 0.1);

				@include media-breakpoint-down(xl) {
					bottom: r(-23px);
					right: 9px; }
				@media (min-width: 1500px) {
					right: -10px; } }
			&:before {
				// box-shadow: -6px 5px 5px 0px rgba(0, 0, 0, 0.1)
				z-index: -1;
				background: color-get($mau,trang);
				position: absolute;
				display: block;
				content: "";
				width: 100%;
				height: 100%;
				bottom: -1rem;
				right: -4rem;
				@media (min-width: 1500px) {
					-webkit-clip-path: circle(33% at 76% 17.5%);
					clip-path: circle(33% at 76% 17.5%); }
				@include media-breakpoint-down(xl) {
					-webkit-clip-path: circle(27% at 69% 32.5%);
					clip-path: circle(27% at 69% 32.5%);
					bottom: -1.5rem; }
				@include media-breakpoint-down(lg) {
					-webkit-clip-path: circle(45% at 44% 22.5%);
					clip-path: circle(45% at 44% 22.5%);
					right: -3rem;
					bottom: r(-23px); }

				@include media-breakpoint-down(md) {
					@include wh(0); } }
			img {
				@include media-breakpoint-down(xl) {
					max-width: 15rem; }

				@include media-breakpoint-down(lg) {
					max-width: 6rem;
					transform: scale(1.2); }
				@include media-breakpoint-down(md) {
					max-width: 4rem; } } }
		.mainnav {

			width: 100%;
			flex-direction: column;
			display: flex;
			.nav-top {
				z-index: 9999;
				position: relative;
				display: flex;
				height: r(40px);
				align-items: center;
				@include media-breakpoint-down(md) {
					height: 100%; }

				.btn-open-menu {
					cursor: pointer;
					display: none;
					@include media-breakpoint-down(md) {
						display: block;
						margin-left: 1rem;
						@include flex-center();
						em {
							font-size: r(31px);
							color: color-get($mau,dark);
							&:last-child() {
								transition-delay: .2s;
								display: none;
								transition: .5s all ease-in-out; } }
						&.active {
							em {
								&:first-child() {
									display: none; }
								&:last-child() {
									display: block;
									transform: rotate(135deg); } } } } }

				.hotline, .mail {
					@include pad(0 r(10px));
					@include media-breakpoint-down(md) {
						@include pad(0); }
					a {
						padding: 0;
						height: 100%;
						color: color-get($mau,trang);
						@include media-breakpoint-down(md) {
							color: color-get($mau,main); }
						h4 {
							color: color-get($mau,main);
							font-weight: 600;
							@include fz(r(15px));
							i, em {

								@include fz(r(20px));
								color: color-get($mau,light); } }
						&:hover {
							text-decoration: none; } } }
				.mail {
					border-right: 1px solid color-get($mau,xam-c);
					border-left: 1px solid color-get($mau,xam-c);
					a {
						h4 {
							font-weight: 300;
							color: color-get($mau,text-9);
							i, em {
								@include fz(r(15px));
								padding-right: 6px; }
							&:hover {
								color: color-get($mau,main); } } } }
				.hotline {
					@include media-breakpoint-down(md);
					@include media-breakpoint-down(xs) {
						h4 {
							font-size: 0 !important;
							em, i {
								font-size: r(15px); } } } }
				.mail {
					@include media-breakpoint-down(md) {
						border: 0;
						margin-left: 1rem; }

					@include media-breakpoint-down(xs) {
						h4 {
							font-size: 0 !important;
							em, i {
								font-size: r(15px); } } } }


				ul {
					@include flex-center();

					li {
						@include pad(2px  r(8px));
						height: 60%;
						a {
							transition: .3s all ease-in-out;
							padding: 0;
							height: 100%;
							@include flex-center();
							color: color-get($mau,xam-c);
							&.active {
								color: color-get($mau,main); }
							&:hover {
								color: color-get($mau,main); } }
						&:first-child {
							padding-left: 11px; }

						&:last-child {
							padding-right: 11px;
							border-right: 1px solid  color-get($mau,xam-c); }

						&:hover {
							a {
								opacity: 1; } } } }

				.language {
					transition: initial;
					position: relative;
					margin-left: 1rem;
					a {
						display: flex;
						align-items: center; } } }

			.nav-bottom {
				@include media-breakpoint-up(lg) {
					border-top-left-radius: 10px; }
				background: #006ab6;
				transition: .5s all ease-in-out;
				position: relative;
				display: flex;
				align-items: center;
				justify-content: flex-end;
				@include media-breakpoint-down(md) {
					height: 100vh;
					justify-content: initial;
					background: color-get($mau, light);
					position: fixed;
					top: r(47px);
					flex-direction: column;
					right: 0;
					width: r(300px);
					transform: translateX(100%);
					transition: .5s all ease-in-out;
					&.active {
						transition: .5s all ease-in-out;
						transform: translateX(0%);
						@include media-breakpoint-down(xs) {
							transition-delay: .1s; } } }

				@include media-breakpoint-down(xs) {
					width: 80%;
 }					// transition-delay: .25s
				&:before {
					z-index: -1;
					background: #006ab6;
					position: absolute;
					display: block;
					content: "";
					width: 100%;
					height: 100%;
					left: -10.2rem;
					@media (min-width: 1500px) {
						-webkit-clip-path: circle(10% at 19.4% 165.6%);
						clip-path: circle(10% at 19.6% 165.6%);
						left: -12rem; }
					@include media-breakpoint-down(xl) {
						-webkit-clip-path: circle(10% at 22.7% 132.6%);
						clip-path: circle(10% at 22.7% 132.6%); }
					@include media-breakpoint-down(lg) {
						-webkit-clip-path: circle(10% at 22.7% 128.6%);
						clip-path: circle(10% at 22.7% 128.6%); }

					@include media-breakpoint-down(md) {
						@include wh(0); } }
				&:after {
					z-index: -1;
					background: #006ab6;
					position: absolute;
					display: block;
					content: "";
					width: 100%;
					height: 100%;
					left: 100%; }

				ul {
					justify-content: space-between;
					@include media-breakpoint-down(md) {
						top: 4rem;
						flex-direction: column;
						right: 0;
						width: 100%;
						flex-wrap: nowrap; }
					li {
						// &:first-child
						// 	a
						// 		padding-left: 5rem
						@media (min-width: 1500px) {
							&:first-child {
								a {
									padding-left: 5rem; } } }

						// +media-breakpoint-down(xl)
						@media (max-width: 1200px) {
							&:first-child {
								a {
									padding-left: 1rem; } } }

						@include media-breakpoint-down(md) {
							@include pad(.7rem null);
							&:first-child {
								padding-top: 1.4rem;
								a {
									padding-left: .75rem; } }
							&:last-child {
								padding-bottom: 1.4rem; } }
						&.active {
							a {
								text-shadow: -.25px -.25px 0 color-get($mau,trang), .25px .25px color-get($mau,trang); } }

						a {
							text-transform: uppercase;
							color: color-get($mau,trang);
							@include fz(r(15px));
							position: relative;
							transition: .3s all ease-in-out;
							text-shadow: -.25px -.25px 0 transparent, .25px .25px transparent;
							@media (min-width: 1500px) {
								@include pad(r(10px) r(28px)); }
							@include media-breakpoint-down(xl) {
								@include pad(r(10px) .75rem); }

							@include media-breakpoint-down(lg) {
								font-size: r(14px); }
							@include media-breakpoint-down(md) {

								@include pad(0  null);
								font-weight: 400;
								color: color-get($mau,trang); }
							&:hover {
								@include media-breakpoint-up(lg) {
									text-shadow: -.25px -.25px 0 color-get($mau,trang), .25px .25px color-get($mau,trang); }
								@include media-breakpoint-down(md) {
									color: color-get($mau, text-3); } } } } }


				.search {
					cursor: pointer;
					padding-left: r(28px);
					i {
						font-size: r(22px);
						color: #6cade3; }
					@include media-breakpoint-down(md) {
						display: none; } }
				.searchbox {
					// display: none
					position: absolute;
					width: 200px;
					opacity: 0;
					top: -100%;
					display: flex;
					@include media-breakpoint-up(lg) {
						right: 3rem;
						transition: .3s all ease-in-out; }
					@include media-breakpoint-down(md) {
						@include pad(0 1rem);
						opacity: 1;
						position: relative;
						width: 100%;
						margin-bottom: 1.4rem;
						top: initial; }
					@include media-breakpoint-down(sm) {
						width: 100%; }
					input {
						border: 1px solid color-get($mau,main);
						border-radius: 0.25rem 0 0 0.25rem;
						border-right: 0;
						padding: .5rem 1rem;
						&:hover, &:active, &:focus {
							outline: none; }
						@media (max-width:992px) {
							width: 100%; } }
					.searchbutton {
						i {
							padding: 0 .5rem; } }
					&.active {
						opacity: 1;
						right: 0;
						top: 100%;
						display: flex; }

					button {
						cursor: pointer;
						@media (max-width:992px) {
							display: flex; }
						border: 1px solid color-get($mau,main);
						background: color-get($mau, main);
						border-radius:  0 0.25rem 0.25rem 0;
						@include media-breakpoint-down(md) {
							background: color-get($mau, dark);
							border: 1px solid color-get($mau,dark); }
						i {
							color: lighten(color-get($mau,main),30%); } } } } } } }

// menu responsive

.sidemenu {
	// transition: .5s all ease-in-out

	@include media-breakpoint-down(md) {
		z-index: 9997;
		transform: translateX(1000%);
		height: 100vh;
		display: flex;
		flex-direction: column;
		background: color-get($mau, main);
		position: fixed;
		top: r(47px);
		width: r(60px);
		transition: .5s all ease-in-out;
		margin-left: calc(100% - 300px - 60px);
		&.active {
			transition: .5s all ease-in-out;
			transform: translateX(0%);
			@include media-breakpoint-down(sm) {
				margin-left: calc(100% - 300px - 60px); }
			@include media-breakpoint-down(xs) {
				margin-left:  0; } } }

	@include media-breakpoint-down(xs) {
		transform: translateX(-1000%);
		width: 20%;
		margin-left: 0; }



	.social {
		li {
			width: 100%;
			a {
				text-align: center; } }
		li {
			@include media-breakpoint-down(md);
			&:first-child {
				a {
					padding-top: 1.4rem; } }
			// &:last-child
			// 	a
			// 		padding-bottom: 1.4rem
			a {
				@include pad(.6rem null);
				font-weight: 400;
				color: color-get($mau,trang); } } }
	.language {
		a {
			// color: color-get($mau,trang)
			@include flex-center();
			flex-direction: column;
			// span
 }			// 	display: none
		.dropdown {
			padding-top: .6rem;
			transition: .3s all ease-in-out;
			.dropdown-toggle {
				transition: .3s all ease-in-out;
				color: color-get($mau,trang);
				font-weight: 400;
				&:hover {
					text-decoration: none; }
				&:after {
					border: 0; } }
			.dropdown-menu {
				// transform: translate3d(0px, 24px, 0px)
				transition: .3s all ease-in-out;
				min-width: r(60px);
				margin-top: .5rem;
				@include media-breakpoint-down(xs) {
					min-width: 100%;
					transform: translate3d(0px, 34px, 0px) !important; }
				&:after {
					display: block;
					position: absolute;
					content: "";
					width: 0;
					height: 0;
					top: -.3rem;
					left: 50%;

					transform: translateX(-50%);
					border-right:  .3em solid transparent;
					border-left:  .3em solid transparent;
					border-bottom: .3em solid color-get($mau,trang); }

				.dropdown-item {
					font-size: .875rem;
					@include pad( null 0);
					@include media-breakpoint-down(md) {
						color: color-get($mau,main); }
					&.active {
						background: color-get($mau, main);
						color: #ffffff; } } } } } }


.sidemenu-backdrop {
	position: fixed;
	background-color: #000000;
	opacity: .75;
	cursor: pointer;
	height: 100vh;
	width: 100vw;
	z-index: 996;
	top: r(47px);
	left: 200rem;
	transition: .3s ease-in-out;
	transition-delay: .2s;
	@include media-breakpoint-down(xs) {
		width: 0; }
	&.active {
		left: 0; }
	@include media-breakpoint-up(lg) {
		display: none; } }
