.cacbuoctuyendung-1 {
	background: #f2f2f2;
	@include pad(2rem null);
	@include media-breakpoint-up(lg) {
		@include pad(r(80px)); }
	.list-item {
		margin-top: r(30px);
		@include mar(null -5px);
		.item {
			@include pad(null 5px);
			figure {
				@include pad(2rem 1rem 1rem 1rem);
				height: 100%;
				background: #fff;
				text-align: center;
				.icon {
					height: 60px; }
				figcaption {
					.title {
						margin-top: .5rem;
						@include fz(r(15px));
						text-transform: uppercase;
						@include fw(600);
						color: #1173b8; }
					.desc {
						margin-top: .75rem; } }
				.step {
					margin-top: 1rem;
					display: flex;
					justify-content: flex-end;
					p {
						color: #666666;
						i {
							margin-left: .4rem; } } } }
			+ .item {
				margin-top: 1rem; } } }
	.list-button {
		margin-top: (r(35px));
		text-align: center;
		a {
			background: #1173b8;
			color: #fff;
			@include pad(.75rem 2rem);
			@include mar(5px);
			&:nth-child(2) {
				background: #07558c; } } }
	@include media-breakpoint-up(lg) {
		.list-button {
			text-align: left;
			a {
				min-width: 240px;
				@include mar(0);
				+ a {
					margin-left: r(50px); } } }
		.list-item {
			.item {
				+ .item {
					margin-top: 0; }
				figure {
					@include pad(r(80px) r(30px) 1rem r(30px));
					display: flex;
					flex-direction: column;
					figcaption {
						display: flex;
						flex-direction: column;
						height: 100%; }
					.step {
						margin-top: 2rem; }
					&:hover {
						background: #1173b8;
						color: #fff;
						.icon {
							img {
								filter: brightness(0) invert(1); } }
						figcaption {
							.title {
								color: #fff; } }
						.step {
							p {
								color: #fff; } } } } } } } }
