.hotro-1 {
	@include pad(2rem null);
	background: #f2f2f2;
	@include media-breakpoint-up(lg) {
		@include pad(r(80px) null); }
	.question-wrapper {
		margin-top: r(50px);
		.item {
			+ .item {
				margin-top: 10px; }
			background: #fff;
			@include fz(r(15px));
			.title {
				user-select: none;
				cursor: pointer;
				display: flex;
				justify-content: space-between;
				align-items: center;
				@include pad(1rem r(20px));
				h5 {
					@include fz(r(15px));
					@include fw(700); }
				em {
					@include fz(r(20px));
					transition: .3s all ease-in-out; }
				&.active {
					em {
						transform: rotate(180deg); } } }
			.content {
				border-top: 1px solid #f2f2f2;
				@include pad(r(20px) r(30px));
				color: #aaaaaa;
				display: none; } } } }
