@function r($size) {
	@return ($size/16px)*1rem; }

@mixin flex-center() {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center; }

@mixin wh($value) {
	width: $value;
	height: $value; }

@mixin line($line) {
	-webkit-line-clamp: $line;
	overflow: hidden;
	display: -webkit-box;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical; }

@mixin mdi($content) {
	content: $content;
	font-size: inherit;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	line-height: inherit;
	text-rendering: auto;
	display: inline-block;
	-webkit-font-smoothing: antialiased;
	font-family: "Material Design Icons"; }

@mixin linearicons($content) {
	content: $content;
	speak: none;
	line-height: 1;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	-webkit-font-smoothing: antialiased;
	font-family: 'Linearicons-Free'; }
