.phanam-nav {
	.nav {
		align-items: center;
		justify-content: center;
		.nav-item {
			@include pad(0 r(8px) r(8px));

			@include media-breakpoint-down(sm) {
				padding-left: 0; }
			.nav-link {
				@include pad(.75rem 1rem);
				@include bor-rad(100px);
				@include fw(500);
				text-transform: uppercase;
				position: relative;
				color: #a4a4a4;
				transition: .4s all ease-in-out;
				font-size: .9rem;
				box-shadow: 0 0 0 1px inset #e1e1e1; }
			&.active, &:hover {
				.nav-link {
					background: #1173b8;
					color: #fff;
					box-shadow: none;
					@include fw(600); } } } } }
