.phanam-home-1 {
	overflow: hidden;
	// margin-bottom: 10px
	position: relative;
	.block-wrapper {
		display: flex;
		@include media-breakpoint-down(sm) {
			flex-direction: column; }
		.info-wrapper {
			position: relative;
			flex: 0 0 35%;
			color: color-get($mau,trang);
			@include pad(r(110px) r(50px) r(85px) 0);
			@include media-breakpoint-down(lg) {
				@include pad(r(60px) r(50px) r(60px) 0); }
			@include media-breakpoint-down(md) {
				flex: 0 0 50%;
				@include pad(2rem 2rem 2rem 0); }
			@include media-breakpoint-down(sm) {
				flex: 0 0 100%;
				@include pad(2rem); }
			@include media-breakpoint-down(xs) {
				@include pad(2rem 1rem); }
			&:after {
				background: #0a558a;
				height: 100%;
				display: block;
				position: absolute;
				content: "";
				z-index: -1;
				top: 0;
				right: 0;
				width: 100rem;
				box-shadow: 0px 0px 49px 0px rgba(0,0,0,0.29);
				@include media-breakpoint-down(sm) {
					right: -50%; } }
			h2 {
				text-transform: uppercase;
				@include fz(r(30px));
				font-weight: 700;
				@include media-breakpoint-down(sm) {
					@include fz(1.4rem); } }
			p {
				@include fz(r(15px));
				@include fw(300);
				@include pad(r(25px) null);
				@include media-breakpoint-down(sm) {
					font-size: 1rem; } }
			.btn-more {
				position: relative;
				font-weight: 300;
				text-transform: uppercase;
				opacity: .5;
				width: r(176px);
				height: r(36px);
				@include fz(r(15px));
				@include flex-center();
				color: color-get($mau,trang);
				border: 2px solid color-get($mau,trang);
				border-radius: r(20px);
				overflow: hidden;
				transition: .5s all ease-in-out;
				&:hover {
					background: black; } } }


		.banner-info-wrapper {
			// margin-left: 10px
			flex: 0 0 65%;
			display: flex;
			@include media-breakpoint-down(sm) {
				flex: 0 0 100%;
				position: relative;
				margin: 0; }
			.banner {
				position: absolute;
				top: 0;
				width: 100%;
				height: 100%;
				z-index: -2;
				@include media-breakpoint-down(sm) {
					width: 100vw;
					margin-left: calc(-50vw + 50%); }
				@include media-breakpoint-up(lg) {
					display: block;
					width: calc((100vw - 960px)/2 + 620px); }
				@include media-breakpoint-up(xl) {
					width: calc((100vw - 1140px)/2 + 736px); }
				@media (min-width: 1500px) {
					width: calc((100vw - 1400px)/ 2 + 910px); } }

			.spec-info {
				@include pad(r(80px) null r(80px) r(80px));
				@include media-breakpoint-down(md) {
					@include pad(2rem null 2rem 2rem); }
				@include media-breakpoint-down(sm) {
					@include pad(2rem); }
				@include media-breakpoint-down(xs) {
					@include pad(2rem 1rem); }
				position: relative;
				// flex: 0 0 60%
				display: flex;
				flex-direction: column;
				height: 100%;
				justify-content: center;
				@include media-breakpoint-up(lg) {
					margin-left: auto;
					justify-content: space-between; }
				&:after {
					background: rgba(0,57,81,.7);
					height: 100%;
					display: block;
					position: absolute;
					content: "";
					z-index: -1;
					top: 0;
					left: 0;
					width: 100rem;
					@include media-breakpoint-down(sm) {
						left: -50%; } }
				figure {
					@include pad(1rem null);
					transition: .4s all ease-in-out;
					display: flex;
					align-items: center;
					border-bottom: 1px dashed color-rgba(color-get($mau,trang),.2);
					figcaption {
						h4 {
							padding-left: 1rem;
							@include fz(r(18px));
							color: color-get($mau,trang); } }
					&:last-child {
						border-bottom: 0; }
					&:hover {
						// padding-left: 1rem
						transform: translateX(-1rem); } } } } } }
