.lienhe-1 {
	@include pad(2rem null);
	background: #f2f2f2;
	@include media-breakpoint-up(lg) {
		@include pad(r(80px) null); }
	.contact-wrapper {
		margin-top: r(40px);
		.location-wrapper {
			box-shadow: 0 0 10px #ccc;
			max-height: 300px;
			overflow-y: auto;
			@include pad(r(30px) null null);
			background: url('../img/bg-map.png');
			.logo {
				text-align: center;
				img {
					max-width: 150px; } }
			&::-webkit-scrollbar {
				width: 10px;
				background-color: #f4f4f4; }
			&::-webkit-scrollbar-track {
				background-color: #f4f4f4; }
			&::-webkit-scrollbar-thumb {
				background-color: #1173b8; }
			.list-item {
				margin-top: 1rem;
				.item {
					cursor: pointer;
					transition: .4s all ease-in-out;
					@include pad(1rem r(30px));
					.name {
						p {
							@include fz(r(15px));
							span {
								@include fw(700);
								color: #1173b8; } }
						margin-bottom: r(15px); }
					.phone {
						align-items: center;
						@include fz(r(15px));
						display: flex;
						flex-wrap: wrap;
						em, span {
							color: #1173b8;
							@include fw(600);
							margin-right: .3rem; } }
					+ .item {
						border-top: 1px dashed #333; }
					&:hover {
						box-shadow: 0 0 20px 4px #918989; } } } }
		.map-wrapper {
			margin-top: 1rem;
			iframe {
				width: 100%;
				height: 400px; } } }
	@include media-breakpoint-up(lg) {
		.contact-wrapper {
			.location-wrapper {
				max-height: 515px;
				.list-item {
					.item {
						.name {
							p {
								@include fz(r(18px)); } }
						.phone {
							@include fz(r(18px)); } } } }
			.map-wrapper {
				padding-left: 15px;
				margin-top: 0;
				height: 100%;
				iframe {
					height: 100%; } } } } }
