.cohoinghenghiep-1, .sukiennoibat-1 {
	@include pad(2rem null);
	@include media-breakpoint-up(lg) {
		@include pad(r(85px) null); }
	.content {
		margin-top: r(40px);
		p {
			+ p {
				margin-top: 1rem; } } }
	.video {
		margin-top: 2rem;
		border: 10px solid #9ec3dd;
		position: relative;
		img {
			width: 100%; }
		.play-button {
			@include pos(abs, 50% null null 50%);
			transform: translate(-50%, -50%);
			img {
				max-width: 50px;
				@include media-breakpoint-up(lg) {
					max-width: 100px; } } } } }
