
.phanam-slider-1 {
	// min-height: r(360px)
	// +media-breakpoint-down(sm)
	// 	min-height: r(260px)
	// box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.29)

	.list-items {
		margin-bottom: 0;
		.item {
			img {
				width: 100%; } }
		.slick-arrow {
			opacity: 0;
			transition: .4s all ease-in-out;
			background: rgba(0,0,0,.3);
			color: rgba(255,255,255,.3);
			padding: .85rem 1rem;
			width: initial;
			height: initial;
			z-index: 100;
			&:before {
				font-family: 'Font Awesome 5 Free';
				font-weight: 900;
				@include fz(1rem); }
			&.slick-prev {
				left: 0;
				&:before {
					content: '\f053'; } }
			&.slick-next {
				right: 0;
				&:before {
					content: '\f054'; } }
			&.slick-disabled {
				&:before {
					@include opacity(.5); } }
			&:hover {
				background: color-get($mau,main);
				color: color-get($mau,trang); } }
		&:hover {
			.slick-arrow {
				opacity: 1; } }
		// Dots
		.slick-dots {
			@include pos(abs, null null 1rem 0);
			width: 100%;
			li {
				width: .75rem;
				height: .75rem;
				@include mar(.5rem);
				&:focus {
					outline: none; }
				button {
					@include pad(0);
					width: .75rem;
					height: .75rem;
					&:before {
						content: '';
						background: darken(color-get($mau,trang),5%);
						@include bor-rad(50%);
						width: .75rem;
						height: .75rem; } }
				&.slick-active {
					button {
						&:before {
							background: color-get($mau,main); } } } } } } }


