.phanam-home-2 {
	background: #ade0ff;
	@include pad(r(120px) null);
	@include media-breakpoint-down(md) {
		@include pad(r(60px) null);
		@include pad(r(60px) null); }
	@include media-breakpoint-down(sm) {
		@include pad(2.875rem null); }

	.wrapper {
		@include flex-center();
		flex-direction: column;
		h2 {
			@include head-title(dark);
			margin-bottom: 1rem; }
		p {
			@include fz(r(18px)); }
		@include media-breakpoint-down(sm) {
			font-size: 1rem;
			padding-top: 1rem; }
		.btn-more {
			position: relative;
			font-weight: 300;
			text-transform: uppercase;
			opacity: .75;
			margin-top: r(40px);
			width: r(176px);
			height: r(36px);
			@include fz(r(15px));
			@include flex-center();
			color: #6da5cd;
			border: 2px solid #6da5cd;
			border-radius: r(20px);
			overflow: hidden;
			transition: .5s all ease-in-out;
			&:hover {
				background: rgba(0,0,0,.6);
				color: #fff; }
			@include media-breakpoint-down(sm) {
				background: transparent;
				box-shadow: 0px 0px 0px 50px color-get($mau,dark) inset;
				color: #6da5cd;
				opacity: 1; } } } }
