.dichvu-2 {
	overflow-x: hidden;
	.wrapper {
		.img {
			width: 100%;
			img {
				width: 100%; } }
		.content {
			@include pad(2rem 1.5rem);
			background: #0a558a;
			color: #fff;
			height: 100%;
			.title {
				h4 {
					@include fz(r(24px));
					text-transform: uppercase;
					@include fw(600); }
				margin-bottom: 1rem; }
			.desc {
				p {
					@include fz(r(15px)); } } } }
	@include media-breakpoint-up(lg) {
		.wrapper {
			&:nth-child(even) {
				[class*="col-"] {
					&:first-child {
						order: 2; } }
				.content {
					background: #72abd7; } }
			.content {
				display: flex;
				justify-content: center;
				flex-direction: column;
				@include pad(2rem); }
			.img {
				img {
					object-fit: cover;
					max-height: 500px; } } } }
	@include media-breakpoint-up(xl) {
		.wrapper {
			.content {
				@include pad(null r(95px)); } } } }
