.phanam-banner-1 {
	box-shadow: 0px 0px 49px 0px rgba(0,0,0,0.29);
	background: color-get($mau, bg);
	.list-items {
		margin-bottom: 0;
		.slick-dots {
			display: none; }
		.item {
			position: relative;
			img {
				width: 100%;
				min-height: r(360px);

				@include media-breakpoint-down(md) {

					object-fit: cover;
					min-height: 16.5rem; }
				@include media-breakpoint-down(sm) {
					min-height: r(260px); } }
			&:after {
				position: absolute;
				display: block;
				@include wh(100%);
				background-image: linear-gradient(270deg, transparent 43%, #032035 198%);
				content: "";
				top: 0; } } } }
