.phanam-home-5 {
	figure {
		position: relative;
		overflow: hidden;
		height: 100%;
		.boxzoom {
			height: 100%;
			overflow: hidden;
			img {
				height: 100%;
				width: 100%;
				transition: .3s all ease-in-out; } }
		figcaption {
			z-index: 1000;
			position: absolute;
			bottom: r(35px);
			display: flex;
			justify-content: flex-start;
			transition: .3s all ease-in-out;
			flex-direction: column;
			color: color-get($mau,trang);
			text-align: left;
			// +pad(null r(100px))
			// padding: 0 r(100px)
			padding-left: r(100px);
			padding-right: r(40px);
			h3 {
				font-weight: 600;
				font-size: r(20px);
				text-transform: uppercase; }
			p {
				padding-top: 1rem;
				font-size:  r(15px); } }
		@include media-breakpoint-up(lg) {
			p {
				transition: .4s all ease-in-out;
				height: 0;
				overflow: hidden; }

			&:hover {
				img {
					transform: scale(1.1); }
				p {
					height: 5rem; } } }
		@include media-breakpoint-down(md) {
			figcaption {
				@include pad(null 3rem); } }
		@include media-breakpoint-down(sm) {
			figcaption {
				@include pad(null 2rem); } }
		&:after {
			position: absolute;
			display: block;
			@include wh(101%);
			content: "";
			background-image: linear-gradient(transparent 43%,#074876 118%);
			bottom: -1px; } } }
