.vitriungtuyen-1 {
	@include pad(2rem null);
	background: #f2f2f2;
	@include media-breakpoint-up(lg) {
		@include pad(r(95px) null); }
	.form-wrapper {
		.form-group {
			margin-bottom: 0;
			@include pad(r(45px) null);
			&:first-child {
				padding-top: 0; }
			&:last-child {
				padding-bottom: 0; }
			+ .form-group {
				border-top: 1px solid #bcbcbc; }
			.title {
				margin-bottom: r(25px);
				h2 {
					@include fz(r(24px));
					text-transform: uppercase;
					color: #1272b4;
					@include fw(700); } }
			.form-item {
				display: flex;
				flex-wrap: wrap;
				@include mar(null -5px);
				@include media-breakpoint-up(lg);
				flex-wrap: nowrap;
				.item {
					width: 100%;
					@include mar(null 5px);
					label {
						@include fz(r(15px));
						margin-bottom: r(6px);
						span {
							color: #ff0315; } }
					.input-group {
						width: auto;
						display: flex;
						flex-wrap: nowrap;
						@include mar(null -5px);
						input {
							@include mar(null 5px); } }
					input {
						@include pad(.75rem 1rem);
						width: 100%;
						background: none;
						border: 1px solid #bcbcbc;
						@include bor-rad(5px);
						@include fz(r(15px)); } }
				.upload {}
				.upload-button {
					margin-left: .6rem;
					display: inline-block;
					position: relative;
					button {
						background: #006ab6;
						color: #fff;
						border: 0;
						@include pad(.45rem 1rem);
						@include bor-rad(5px);
						@include fz(r(14px)); }
					input {
						opacity: 0;
						@include pos(abs, 0 null null 0); } }
				.note {
					@include fz(r(14px));
					width: 100%;
					@include media-breakpoint-up(md) {
						display: inline;
						margin-left: 2rem; } }
				+ .form-item {
					margin-top: r(15px); } }
			.capcha {
				align-items: flex-end;
				@include media-breakpoint-up(md) {
					.item {
						flex: 0 0 33.3333%; } }
				.capcha-code {
					display: flex;
					flex: 0 0 50%;
					.code {
						img {
							width: 160px;
							@include media-breakpoint-up(md) {
								width: auto; } } }
					.swap {
						cursor: pointer;
						user-select: none;
						margin-left: 1rem;
						em {
							@include fz(2rem);
							@include media-breakpoint-up(md) {
								@include fz(r(50px)); }
							@include lh(3rem);
							color: #006ab6; } } } }
			.list-button {
				margin-top: 2rem;
				button {
					background: #006ab6;
					color: #ffffff;
					border: 0;
					@include fz(r(15px));
					@include bor-rad(5px);
					@include pad(.65rem 1.3rem);
					+ button {
						margin-left: 5px; } } } } } }
