.huongdannophoso-1 {
	@include pad(2rem null);
	@include media-breakpoint-up(lg) {
		@include pad(r(80px) null); }
	.tutorial-wrapper {
		margin-top: 2rem;
		.item {
			+ .item {
				margin-top: 1.5rem; }
			.title {
				em {
					color: #1173b8;
					@include fz(r(15px));
					margin-right: .3rem; }
				margin-bottom: r(15px);
				h3 {
					display: inline;
					@include fz(r(15px));
					text-transform: uppercase;
					@include fw(700);
					// +lh(r(15px))
					color: #1173b8;

					span {
						text-transform: lowercase;
						@include fw(500);
						color: #333; } } }
			.content {
				p {
					@include fz(r(15px));
					+ p {
						margin-top: .75rem; }
					a {
						color: #1173b8; } }
				ul {
					list-style: none;
					padding-left: 0;
					counter-reset: my-sec-counter;
					li {
						+ li {
							margin-top: r(20px); }
						h4 {
							@include fz(r(15px));
							margin-bottom: .25rem;
							@include fw(700);
							&:before {
								counter-increment: my-sec-counter;
								content: counter(my-sec-counter) ". "; } }
						p {
							+ p {
								margin-top: 0; } } }
					@include media-breakpoint-up(md) {
						column-count: 2; } } } } }
	@include media-breakpoint-up(lg) {
		.tutorial-wrapper {
			margin-top: r(60px); } } }
