.phanam-service-2 {
	overflow: hidden;
	position: relative;
	.block-wrapper {
		position: relative;
		display: flex;
		@include media-breakpoint-down(sm) {
			flex-direction: column; }
		&:nth-child(even) {
			@include media-breakpoint-up(md) {
				flex-direction: row-reverse; }
			.info-wrapper {
				padding: r(30px) 0 r(30px) r(130px);
				@include media-breakpoint-down(lg) {
					@include pad(r(60px) 0 r(60px) r(60px)); }
				@include media-breakpoint-down(md) {
					flex: 0 0 50%;
					@include pad(2rem 0 2rem 2rem); }
				@include media-breakpoint-down(sm) {
					flex: 0 0 100%;
					padding-left: 0; }
				@include media-breakpoint-down(xs) {
					@include pad(2rem 1rem); }
				&:after {
					left: -50%;
					right: auto;
					background: color-get($mau, light); }
				.btn-more {
					color: color-get($mau,trang);
					border: 2px solid color-get($mau,trang); } }


			.banner-info-wrapper {
				@include media-breakpoint-down(sm) {
					min-height: r(200px); }
				.banner {
					// left: -6rem
					right: 35%;
					@include media-breakpoint-down(lg) {
 }						// left: -9rem
					@include media-breakpoint-down(md) {
						right: 50%;
						left: auto; }
					@include media-breakpoint-down(sm) {
						right: 0;
						left: 0; } } } }


		.info-wrapper {
			position: relative;
			flex: 0 0 35%;
			color: color-get($mau,trang);

			display: flex;
			justify-content: center;
			flex-direction: column;
			text-align: left;
			@include media-breakpoint-up(xl) {
				// align-items: center
				height: r(450px);
				@include pad(r(30px));
				padding-left: 0; }
			@include media-breakpoint-down(lg) {
				height: r(450px);
				// +pad(r(30px))
				padding-left: 0; }
			@include media-breakpoint-down(md) {
				// height: r(250px)
				height: auto;
				flex: 0 0 50%;
				@include pad(2rem);
				padding-left: 0; }
			@include media-breakpoint-down(sm) {
				flex: 0 0 100%;
				@include pad(2rem); }
			@include media-breakpoint-down(xs) {
				@include pad(2rem 1rem); }
			&:after {
				background: color-get($mau, dark);
				height: 100%;
				display: block;
				position: absolute;
				content: "";
				z-index: -1;
				top: 0;
				right: 0;
				width: 500%;
				@include media-breakpoint-down(sm) {
					right: -50%; } }
			.btn-more {
				margin-top: r(25px);
				position: relative;
				font-weight: 300;
				text-transform: uppercase;
				width: r(180px);
				height: r(40px);
				@include fz(r(15px));
				@include flex-center();
				color: color-get($mau,light);
				border: 2px solid color-get($mau,light);
				border-radius: r(20px);
				overflow: hidden;
				// background: color-get($mau, trang)
				transition: .5s all ease-in-out;
				@include media-breakpoint-down(sm) {
					width: 8.5rem; }

				@include media-breakpoint-up(lg) {
					&:hover {
						background: transparent;
						box-shadow: 0px 0px 0px 50px color-get($mau,trang) inset;
						color: color-get($mau,dark); } } }
			h2 {
				@include head-title(trang);
				margin-bottom: 1rem;
				text-align: left;
				font-size: r(24px); }

			p {
				color: color-get($mau,trang);
				@include line(5); } }



		.banner-info-wrapper {
			// margin-left: 10px
			flex: 0 0 65%;
			display: flex;
			@include media-breakpoint-down(md) {
				flex: 0 0 50%; }
			@include media-breakpoint-down(sm) {
				flex: 0 0 100%;
				position: relative;
				margin: 0;
				min-height: r(200px); }
			.banner {
				position: absolute;
				top: 0;
				width: 100%;
				height: 100%;
				// z-index: -2
				@include media-breakpoint-down(sm) {
					width: 100vw;
					margin-left: calc(-50vw + 50%); }
				@include media-breakpoint-up(md) {
					width: calc((100vw - 720px)/2 + 360px); }
				@include media-breakpoint-up(lg) {
					display: block;
					width: calc((100vw - 960px)/2 + 620px); }
				@include media-breakpoint-up(xl) {
					width: calc((100vw - 1140px)/2 + 736px); }
				@media (min-width: 1500px) {
					width: calc((100vw - 1400px)/ 2 + 910px); } } } } }
