@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800&subset=vietnamese');
@font-face {
	font-family: 'Material Icons';
	font-style: normal;
	font-weight: 400;
	src: url(https://fonts.gstatic.com/s/materialicons/v41/flUhRq6tzZclQEJ-Vdg-IuiaDsNcIhQ8tQ.woff2) format('woff2'); }


// @import ../node_modules/bootstrap/scss/bootstrap

.phanam-header-1 {
	.Module, .ModuleContent {
		height: 100%; } }

// main
// 	overflow: hidden

p ,ul ,h1,h2,h3,h4,h5,h6,figure,div, * {
	margin: 0;
	padding: 0; }
p {
	font-size: r(15px);
	line-height: r(23px); }
strong {
	font-weight: 600;
	font-size: r(15px); }

html, body {
	font-family:  'Open Sans', sans-serif;
	font-size: 16px;
	line-height: 1.5;
	font-weight: 500; }

.material-icons {
	font-family: 'Material Icons';
	font-style: normal;
	font-weight: 300;
	@include fz(1rem); }

.head-title {
	text-align: center;
	@include fz(r(30px));
	text-transform: uppercase;
	color: color-get($mau,dark);
	font-weight: 600;
	@include media-breakpoint-down(sm) {
		@include fz(1.5rem); } }
@mixin head-title($color) {
	text-align: center;
	@include fz(r(30px));
	text-transform: uppercase;
	color: color-get($mau,$color);
	font-weight: 600;
	@include media-breakpoint-down(sm) {
		@include fz(1.5rem); } }
@media (min-width: 1500px) {
	.container {
		max-width: 1400px; } }
.no-gutters {
	margin-right: 0;
	margin-left: 0;
	.col, [class*="col-"] {
		@include pad(0); } }
.select {
	width: 100%;
	background: #fff;
	position: relative;
	&:after {
		@include mdi('\f140');
		@include pos(abs, 50% 1rem null null);
		transform: translateY(-50%); }
	select {
		@include pad(.5rem 1rem);
		width: 100%;
		background: none;
		border: 0;
		color: #808080;
		@include fz(r(15px));
		-webkit-appearance: none;
		-moz-appearance: none;
		&:focus {
			outline: none; } } }
