.phanam-home-4 {
	background: #6ab0de;
	@include pad(r(120px) null);
	@include media-breakpoint-down(md) {
		@include pad(r(60px) null); }
	@include media-breakpoint-down(sm) {
		@include pad(2.875rem null); }

	.wrapper {
		@include flex-center();
		flex-direction: column;
		h2 {
			@include head-title(dark);
			margin-bottom: .5rem;
			color: #006ab6; }
		p {
			color: color-get($mau,trang);
			@include fz(r(18px));
			max-width: r(750px);
			@include media-breakpoint-down(sm) {
				font-size: 1rem;
				padding-top: 1rem; } }
		.btn-more {
			position: relative;
			font-weight: 300;
			text-transform: uppercase;
			// opacity: .5
			margin-top: r(40px);
			width: r(176px);
			height: r(36px);
			@include fz(r(15px));
			@include flex-center();
			color: #ffff;
			border: 2px solid #9fccec;
			border-radius: r(20px);
			overflow: hidden;
			background: #85bee5;
			transition: .5s all ease-in-out;
			@include media-breakpoint-up(md) {
				&:hover {
					background: transparent;
					box-shadow: 0px 0px 0px 50px color-get($mau,dark) inset;
					color: #fff; } }
			@include media-breakpoint-down(sm) {
				background: transparent;
				box-shadow: 0px 0px 0px 50px color-get($mau,dark) inset;
				color: #6da5cd;
				opacity: 1; } } } }
