.gioithieu2-1 {
	@include pad(2rem null);
	@include media-breakpoint-up(lg) {
		@include pad(r(95px) null); }
	.list-img {
		position: relative;
		@include pad(3rem null null);
		img {
			max-width: 100%;
			user-select: none; }
		.img-2 {
			margin-top: r(70px); }
		.popup-wrapper {
			@include pos(fixed, 0 null null 0);
			background: rgba(0,0,0,.5);
			width: 100%;
			height: 100%;
			z-index: -2;
			transition: .4s all ease-in-out;
			opacity: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			&.active {
				z-index: 3333;
				opacity: 1; }
			.popup-container {
				background: #ffffff;
				@include pad(1.5rem);
				@include mar(.5rem);
				position: relative;
				box-shadow: 0 0 10px #7f7d7d;
				.popup-content {
					.logo {
						img {
							max-width: 160px; } }
					.name {
						h3 {
							@include fz(r(18px));
							color: #3a598b;
							@include fw(600); }
						@include mar(1rem null); }
					.info {
						.item {
							display: flex;
							em {
								@include fw(900);
								color: #1b72b4;
								margin-right: .5rem; }
							p {
								@include fz(r(15px));
								color: #666666; }
							+ .item {
								margin-top: 1rem; } } } }
				.popup-close {
					width: 2rem;
					height: 2rem;
					background: #f2f2f2;
					text-align: center;
					box-shadow: 0 0 8px #b0adad;
					line-height: 2rem;
					@include pos(abs, null 0 100% null); } }
			@include media-breakpoint-up(lg) {
				background: transparent;
				width: auto;
				height: auto;
				position: absolute;
				top: auto;
				bottom: -3rem;
				left: 3rem;
				max-width: 400px;
				.popup-container {
					.popup-close {
						right: -2rem;
						bottom: auto;
						top: 0; } } } } } }
