.phanam-service-1 {
	@include pad(r(89px) null);
	@include media-breakpoint-down(md) {
		@include pad(r(49px) null); }
	@include media-breakpoint-down(md) {
		@include pad(r(25px) null); }
	.wrapper {
		@include flex-center();
		flex-direction: column;
		h2 {
			@include head-title(main);
			margin-bottom: .5rem; }
		img {
			margin-bottom: r(40px); }
		p {
			color: color-get($mau,text-3);
			max-width: r(730px);
			font-size: r(18px);
			@include media-breakpoint-down(sm) {
				font-size: 1rem; } } } }
