.phanam-home-3 {
	figure {
		position: relative;
		overflow: hidden;
		.boxzoom {
			overflow: hidden;
			img {
				width: 100%;
				transition: .5s all ease-in-out; } }
		figcaption {
			z-index: 300;
			// height: r(190px)
			position: absolute;
			bottom: r(50px);
			display: flex;
			justify-content: flex-end;
			transition: .3s all ease-in-out;
			flex-direction: column;
			color: color-get($mau,trang);
			text-align: left;
			// +pad(null r(100px))
			// padding: 0 r(100px)
			padding: 0 3rem;
			transition: .5s all ease-in-out;
			h3 {
				margin-bottom: 1.2rem;
				text-transform: uppercase;
				font-weight: 600;
				font-size: r(20px);

				transition: .5s all ease-in-out; }
			p {
				line-height: 25px;
				overflow: hidden;
				transition: .5s all ease-in-out;
				// visibility: hidden
				// opacity: 0
				height: 0;
				font-size:  r(15px); } }
		@include media-breakpoint-up(lg) {
			&:hover {
				.boxzoom {
					img {
						transform: scale(1.1); } }
				figcaption {
					justify-content: flex-start;
					// // bottom: 0
					p {
						// padding-top: .75rem
						height: 150px;
						// opacity: 1
 }						// visibility: visible
 } } }					// 	display: block
		@include media-breakpoint-down(md) {
			figcaption {
				@include pad(null 3rem);
				p {
					height:  100%; } } }
		@include media-breakpoint-down(sm) {
			figcaption {
				@include pad(null 2rem); } }
		&:after {
			position: absolute;
			display: block;
			@include wh(101%);
			content: "";
			background-image: linear-gradient(transparent 43%,#032035 124%);
			bottom: -1px; } } }
