.daotao-1, .hoatdongvanhoa-1, .trachnhiem-1, .phanam-gioithieu1-3 {
	overflow-x: hidden;
	padding-top: 2rem;
	background: #f2f2f2;
	@include media-breakpoint-up(lg) {
		padding-top: r(60px); }
	.cs-wrapper {
		margin-top: 2rem;
		@include media-breakpoint-up(lg) {
			margin-top: r(40px); }
		.wrapper {
			.img {
				position: relative;
				width: 100%;
				img {
					width: 100%;
					transition: .4s all ease-in-out; }
				transition: .4s all ease-in-out;
				&:hover {
					img {
						transform: scale(1.1); } } }
			.content {
				@include pad(2rem 1.5rem);
				background: url('../img/bg-text.png');
				background-size: cover;
				height: 100%;
				.title {
					h4 {
						color: #333333;
						@include fz(r(24px));
						text-transform: uppercase;
						@include fw(600); }
					margin-bottom: .75rem; }
				.desc {
					p {
						@include fz(r(15px));
						color: #999999; } } } }
		@include media-breakpoint-up(lg) {
			.wrapper {
				&:nth-child(even) {
					[class*="col-"] {
						&:first-child {
							order: 2; } } }
				.content {
					display: flex;
					justify-content: center;
					flex-direction: column;
					@include pad(2rem); }
				.img {
					img {
						object-fit: cover;
						max-height: 500px; } } } }
		@include media-breakpoint-up(xl) {
			.wrapper {
				.content {
					@include pad(null r(95px)); } } } } }
