.bandovieclam-1 {
	@include pad(2rem null);
	background: #f2f2f2;
	@include media-breakpoint-up(lg) {
		@include pad(r(80px) null);
		.jcc {
			justify-content: center; } }
	.map-wrapper {
		margin-top: 2rem;
		img {
			width: 100%; } }
	.location-wrapper {
		background: #dfe8f1;
		@include pad(1.5rem);
		.title {
			text-align: center;
			h4 {
				@include fz(r(15px));
				text-transform: uppercase;
				@include fw(600); } }
		.list-item {
			margin-top: 1.5rem;
			.item {
				+ .item {
					margin-top: 1rem; }
				p {
					@include fz(r(15px));
					i {
						margin-right: .3rem;
						color: #e1171c; }
					span {
						color: #e1171c; } } } } }
	@include media-breakpoint-up(lg) {
		.location-wrapper {
			@include pad(r(45px));
			.list-item {
				display: flex;
				.item {
					flex: 0 0 20%;
					+ .item {
						margin-top: 0; }
					p {
						@include pad(.35rem null); } } } } } }
