.dichvu-3 {
	@include pad(3rem null);
	.content {
		text-align: center;
		margin-bottom: 2rem;
		.title {
			margin-bottom: .75rem;
			h3 {
				@include fz(r(24px));
				color: #006ab6;
				@include fw(700);
				text-transform: uppercase; } } }
	@include media-breakpoint-up(lg) {
		@include pad(r(95px) null);
		.content {
			max-width: 750px;
			@include mar(null auto);
			.title {
				h3 {
					@include fz(r(30px)); } }
			.desc {
				p {
					@include fz(r(18px)); } } } }
	.slider {
		position: relative;
		.swiper-wrapper {
			align-items: center;
			min-height: 200px;
			@include media-breakpoint-up(sm) {
				min-height: 250px; }
			@include media-breakpoint-up(md) {
				min-height: 328px; }
			@include media-breakpoint-up(lg) {
				min-height: 458px; }
			@include media-breakpoint-up(xl) {
				min-height: 654px; }
			.swiper-slide {
				width: 70%!important;
				height: 80%;
				// transition: .4s all ease-in-out
				overflow: hidden;
				position: relative;
				&:after {
					content: '';
					width: 100%;
					height: 100%;
					@include pos(abs, 0 null null 0);
					background: rgba(0,0,0,.7); }
				img {
					transition: .4s all ease-in-out;
					width: 100%;
					height: 100%;
					object-fit: cover;
					&:hover {
						transform: scale(1.1); } }
				&.swiper-slide-active {
					height: 100%;
					&:after {
						display: none; } } } }
		.swiper-navigation {
			margin-top: 1rem;
			display: flex;
			align-items: center;
			justify-content: center;
			div {
				@include mar(null 5px);
				width: 2rem;
				height: 2rem;
				@include lh(2rem);
				text-align: center;
				background: #006ab6;
				color: #fff; }
			@include media-breakpoint-up(md) {
				margin-top: 0;
				div {
					width: auto;
					height: auto;
					@include lh(auto);
					background: transparent;
					z-index: 333;
					cursor: pointer;
					&:hover {
						em {
							color: #006ab6; } }
					&:focus {
						outline: none; }
					em {
						@include fz(r(30px)); }
					&.swiper-prev {
						@include pos(abs, 50% null null 5%);
						transform: translateY(-50%); }
					&.swiper-next {
						@include pos(abs, 50% 5% null null );
						transform: translateY(-50%); } } } } } }
