.tuyendung-1 {
	background: #f2f2f2;
	padding: r(30px) 0;
	@include media-breakpoint-up(lg) {
		padding: r(92px) 0; }
	.title {
		h2 {
			@include fz(20px);
			@include media-breakpoint-up(lg) {
				@include fz(r(30px)); }
			font-weight: 600;
			color: #006ab6;
			text-transform: uppercase; } }
	.time {
		display: flex;
		align-items: center;
		margin-top: r(10px);
		i, span {
			color: #cecece;
			@include fz(r(15px)); }
		span {
			@include fw(500);
			margin: 0;
			margin-left: .25rem; } }
	.content {
		margin-top: 1rem;
		h3, p {
			@include lh(r(25px));
			@include fz(r(15px)); }
		h3 {
			margin-bottom: .2rem;
			font-weight: 700;
			color: #333333; }
		p {
			color: #4c4c4c;
			margin-bottom: 1.3rem; } }
	.more {
		margin-top: r(40px);
		.button {
			.btn-more {
				position: relative;
				font-weight: 700;
				text-transform: uppercase;
				background: #006ab6;
				@include pad(.75rem);
				display: inline-block;
				@include fz(r(15px));
				color: #fff;
				border-radius: 100px;
				overflow: hidden;
				color: #fff;
				transition: .5s all ease-in-out;
				@include media-breakpoint-up(lg) {
					&:hover {
						background: transparent;
						box-shadow: 0px 0px 0px 50px #fff inset, 0 0 0 2px #006ab6;
						color: color-get($mau,dark); } } } }
		.share {
			margin-top: r(25px);
			p {
				font-style: italic;
				@include fw(600);
				@include fz(r(15px));
				display: flex;
				align-items: center;
				a {
					margin-left: 1rem;
					i {
						color: #4129d3;
						@include fz(2rem); }
					+ a {
						margin-left: .5rem; } } } } }
	.other-wrapper {
		background: #fff;
		padding-top: 1.5rem;
		.title {
			@include pad(null 1.5rem); }
		.list-item {
			margin-top: 1rem;
			.item {
				@include pad(.65rem 1.5rem);
				+ .item {
					border-top: 1px solid #d2d3d4; }
				a {
					text-decoration: none;
					display: flex;
					justify-content: space-between;
					align-items: center;
					.time {
						margin-top: 0; }
					p {
						padding-right: 1rem;
						color: #333; } } } }
		@include media-breakpoint-up(lg) {
			padding-top: r(30px);
			.title, .list-item .item {
				@include pad(null r(30px)); }
			.list-item {
				.item {
					a {
						p {
							transition: .4s all ease-in-out; } }
					&:hover {
						a {
							p {
								color: #006ab6; } } } } } } } }
